.nav-tabs {
  padding-left: 30%;
  padding-right: 30%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 8px;
}

.nav-tabs .nav-link {
  font-size: 1.8rem;
  color: darkcyan;
}

.nav-tabs .nav-link.active {
  background-color: lightgray;
  border: none;
}

